:root {
  --bg: #ffffff;
  --text: #111111;
  --card-bg: #f0f0f0;
  --accent: #66d9ef;
  --accent-hover: #4fc3dc;
  --timer: #333;
}

[data-theme="dark"] {
  --bg: #1e1e2f;
  --text: #f5f5f5;
  --card-bg: #2e2e48;
  --accent: #66d9ef;
  --accent-hover: #4fc3dc;
  --timer: #ffd93d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  transition: background 0.3s ease, color 0.3s ease, border 0.3s ease;
}

body {
  background: var(--bg);
  color: var(--text);
  height: 100vh;
  overflow: hidden;
}

.screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  transform: translateX(0%);
}

.carousel-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
  overflow: visible;
  min-height: 500px;
  perspective: 1200px;
  perspective-origin: center center;
}

.carousel-track {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.25, 0.1, 0.25, 1);
}

.player-card {
  background: var(--card-bg);
  --progress-angle: 360deg;
  padding: 0;
  width: 300px; 
  height: 300px;
  border-radius: 50%;
  border: 2px solid var(--accent);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  transform-style: preserve-3d;
  cursor: pointer;
  text-align: center;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  transition: all 0.6s ease;
}

.player-card.current {
  transform: translateZ(0px) scale(1.2);
  background-image: conic-gradient(
      from 1deg at center,
      var(--accent) 0deg,
      var(--accent) var(--progress-angle, 360deg),
      var(--card-bg) var(--progress-angle, 360deg),
      var(--card-bg) 360deg
  );
  z-index: 10;
  opacity: 1;
}

.player-card.prev {
  transform: translateX(-200px) translateZ(-150px) rotateY(45deg) scale(0.8);
  opacity: 0.6;
  z-index: 5;
}

.player-card.next {
  transform: translateX(200px) translateZ(-150px) rotateY(-45deg) scale(0.8);
  opacity: 0.6;
  z-index: 5;
}

.player-card.hidden {
  transform: translateZ(-400px) scale(0.4);
  opacity: 0;
  z-index: 0;
}

.timer {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--timer);
}

button, input, select {
  padding: 0.75rem 1.25rem;
  background: var(--card-bg);
  color: var(--text);
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: 1rem;
}

button:hover, input:hover, select:hover {
  background: var(--accent-hover);
}

.theme-toggle {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text);
  z-index: 3;
}

.theme-toggle:hover {
  color: var(--accent);
}

.menu-button {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: var(--card-bg);
  color: var(--text);
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: 1.25rem;
  z-index: 3;
  }

.menu-button:hover {
  background: var(--accent-hover);
}

.shelf {
  position: absolute;
  top: 0;
  left: -40%;
  width: 40%;
  height: 100vh;
  background: var(--card-bg);
  color: var(--text);
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.2);
  transition: left 0.6s cubic-bezier(0.25, 0.1, 0.25, 1);
  padding: 2rem;
  z-index: 2;
}

.shelf.open {
  left: 0;
}

.player-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: transparent;
  color: var(--text);
}

.player-list li input {
  border: none;
  background: transparent;
  font-size: 1rem;
  color: var(--text);
  width: 100%;
}

.player-list li input:focus {
  background: var(--bg);
  outline: 1px solid var(--accent);
}

.add-player {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  margin-bottom: 1rem;
}

.add-player input {
  flex: 1;
  padding: 0.5rem;
  background: var(--bg);
  border: 1px solid var(--accent);
  color: var(--text);
  border-radius: 6px;
}

.add-player button {
  padding: 0.5rem 1rem;
  background-color: var(--accent);
  color: #111;
  border-radius: 6px;
  white-space: nowrap;
  flex-shrink: 0;
  transition: background-color 0.2s ease;
}

.add-player button:hover {
  background-color: var(--accent-hover);
}

.mode-switch {
display: flex;
align-items: center;
gap: 0.5rem;
margin-top: 1rem;
margin-bottom: 1rem;
}

.mode-switch input {
display: none;
}

.slider {
position: relative;
width: 50px;
height: 24px;
background-color: #ccc;
border-radius: 24px;
transition: background-color 0.3s;
cursor: pointer;
}

.slider::before {
content: "";
position: absolute;
height: 20px;
width: 20px;
left: 2px;
bottom: 2px;
background-color: white;
border-radius: 50%;
transition: transform 0.3s;
}

.mode-switch input:checked + .slider {
background-color: var(--accent);
}

.mode-switch input:checked + .slider::before {
transform: translateX(26px);
}

.mode-label {
font-size: 1rem;
}

.count-toggle {
display: flex;
align-items: center;
justify-content: center;
gap: 1rem;
margin: 1rem 0;
}

.count-toggle label {
display: flex;
align-items: center;
gap: 0.5rem;
}

.count-toggle input {
display: none;
}

.count-slider {
position: relative;
width: 60px;
height: 28px;
background-color: #ccc;
border-radius: 28px;
transition: background-color 0.3s;
cursor: pointer;
}

.count-slider::before {
content: "";
position: absolute;
height: 24px;
width: 24px;
left: 2px;
bottom: 2px;
background-color: white;
border-radius: 50%;
transition: transform 0.3s;
}

.count-toggle input:checked + .count-slider {
background-color: var(--accent);
}

.count-toggle input:checked + .count-slider::before {
transform: translateX(32px);
}
